import React from 'react';
import {Link} from 'react-router-dom'


function Navbar() {
    return (
      <div>
        <nav>
          <ul>
            <Link
              className="home"
              style={{
                textDecoration: "none",
                color: "#FDFFFC",
                fontWeight: "700",
                paddingRight: "50px",
              }}
              to="/zenapp/"
            >
              Home
            </Link>
            <Link
              style={{
                textDecoration: "none",
                color: "#FDFFFC",
                fontWeight: "700",
                paddingRight: "50px",
              }}
              to="/zenapp/music"
            >
              Music
            </Link>
            <Link
              style={{
                textDecoration: "none",
                color: "#FDFFFC",
                fontWeight: "700",
                textAlign: 'center'
              }}
              to="/zenapp/journal"
            >
              Journal
            </Link>
          </ul>
        </nav>
      </div>
    );
}

export default Navbar