import React, { useState } from 'react';
import {db} from '../firebase'


function Journal() {
    let [mood, setMood] = useState('')
    let [journalPost, setJournalPost] = useState('')

    const handleMoodChange = (e) => {
        setMood(e.target.value)
    }
    
    const handleJournalChange = (e) => {
        setJournalPost(e.target.value)
    }

    const handleSubmit = (e) => {
        e.preventDefault()

        db.collection('Mood & Journals').add({
          mood: mood,
          journal: journalPost
        })
        .then(() => {
          alert('Info saved!');
        })
        
        setMood('')
        setJournalPost('')
        // console.log(mood);
        // console.log(journalPost);
    }
    return (
      <div>
        <br />
        <br />
        <h2 className="journal">
          Record your thoughts after your meditation. <br />
          How are you feeling?
          <br /> Recognize any changes in your mood?{" "}
        </h2>
        <form onSubmit={handleSubmit}>
          <div className="select">
            <select id="mood-select" value={mood} onChange={handleMoodChange}>
              <option>Select your mood:</option>
              <option value="Happy">Happy 😁</option>
              <option value="Calm">Calm 😌</option>
              <option value="Sad">Sad 🙁</option>
              <option value="Angry">Angry 😡</option>
              <option value="Indifferent">Indifferent 😐</option>
              <option value="Anxious">Anxious 😰</option>
            </select>
          </div>
          <br />
          <br />
          <textarea value={journalPost} onChange={handleJournalChange} rows="20" cols="75" className="textarea"></textarea>
          <input type="submit" value="Submit" id="submit" />
        </form>
      </div>
    );
}

export default Journal